<template>
  <v-container>
    <v-stepper v-model="e1">
      <v-stepper-header>
        <v-stepper-step
          @click="e1=1"
          step="1"
        >Общая информация</v-stepper-step>

        <v-divider />

        <v-stepper-step
          @click="e1=2"
          step="2"
        >Контакты</v-stepper-step>

        <v-divider />

        <v-stepper-step
          step="3"
          @click="e1=3"
        >Реквизиты</v-stepper-step>

        <v-divider />

        <v-stepper-step
          step="4"
          @click="e1=4"
        >Условия проживания</v-stepper-step>
      </v-stepper-header>

      <v-stepper-items>
        <v-stepper-content step="1">
          <v-card
            class="mb-12"
          >
            <v-row>
              <v-col
                class="pt-0 pb-0"
                cols="4"
              >
                <p class="title">Название</p>
              </v-col>
              <v-col
                class="pt-0 pb-0"
                cols="8"
              >
                <v-text-field
                  dense
                  v-model="name"
                ></v-text-field>
              </v-col>
              <v-col
                class="pt-0 pb-0"
                cols="4"
              >
                <p class="title">Юредическое название</p>
              </v-col>
              <v-col
                class="pt-0 pb-0"
                cols="8"
              >
                <v-text-field
                  dense
                  v-model="jurName"
                ></v-text-field>
              </v-col>
              <v-col
                class="pt-0 pb-0"
                cols="4"
              >
                <p class="title">Имя собственника</p>
              </v-col>
              <v-col
                class="pt-0 pb-0"
                cols="8"
              >
                <v-text-field
                  dense
                  v-model="benificareName"
                ></v-text-field>
              </v-col>
              <v-col
                class="pt-0 pb-0"
                cols="4"
              >
                <p class="title">Тип размещения</p>
              </v-col>
              <v-col
                class="pt-0 pb-0"
                cols="8"
              >
                <v-text-field
                  dense
                  v-model="type"
                ></v-text-field>
              </v-col>
            </v-row>
          </v-card>

          <v-btn
            color="primary"
            to="/sales-channels/list"
          >
            Сохранить
          </v-btn>

          <v-btn text>Выход</v-btn>
        </v-stepper-content>

        <v-stepper-content step="2">
          <v-card
            class="mb-12"
            height="200px"
          >
            <v-row>
              <v-col
                class="pt-0 pb-0"
                cols="4"
              >
                <p class="title">Телефон</p>
              </v-col>
              <v-col
                class="pt-0 pb-0"
                cols="8"
              >
                <v-text-field
                  dense
                  v-model="phone"
                ></v-text-field>
              </v-col>
              <v-col
                class="pt-0 pb-0"
                cols="4"
              >
                <p class="title">E-mail</p>
              </v-col>
              <v-col
                class="pt-0 pb-0"
                cols="8"
              >
                <v-text-field
                  dense
                  v-model="email"
                ></v-text-field>
              </v-col>
              <v-col
                class="pt-0 pb-0"
                cols="4"
              >
                <p class="title">Контактное лицо</p>
              </v-col>
              <v-col
                class="pt-0 pb-0"
                cols="8"
              >
                <v-text-field
                  dense
                  v-model="contactName"
                ></v-text-field>
              </v-col>
            </v-row>
          </v-card>

          <v-btn
            color="primary"
            to="/sales-channels/list"
          >
            Сохранить
          </v-btn>

          <v-btn text>Выход</v-btn>
        </v-stepper-content>

        <v-stepper-content step="3">
          <v-card
            class="mb-12"
          >
          </v-card>

          <v-btn
            color="primary"
            to="/sales-channels/list"
          >
            Сохранить
          </v-btn>

          <v-btn text>Выход</v-btn>
        </v-stepper-content>
        <v-stepper-content step="4">
          <v-card>
            <v-row>
              <v-col
                class="pt-0 pb-0"
                cols="4"
              >
                <p class="title">Стандартное время заезда</p>
              </v-col>
              <v-col
                class="pt-0 pb-0"
                cols="8"
              >
                <v-text-field
                  dense
                  v-model="stdTimeToIn"
                ></v-text-field>
              </v-col>
              <v-col
                class="pt-0 pb-0"
                cols="4"
              >
                <p class="title">Стандартное время выезда</p>
              </v-col>
              <v-col
                class="pt-0 pb-0"
                cols="8"
              >
                <v-text-field
                  dense
                  v-model="stdTimeToOut"
                ></v-text-field>
              </v-col>
              <v-col
                class="pt-0 pb-0"
                cols="4"
              >
                <p class="title">Условия отмены бронирования</p>
              </v-col>
              <v-col
                class="pt-0 pb-0"
                cols="8"
              >
                <v-text-field
                  dense
                  v-model="cancelPerm"
                ></v-text-field>
              </v-col>
              <v-col
                class="pt-0 pb-0"
                cols="4"
              >
                <p class="title">Минимальное количество дней</p>
              </v-col>
              <v-col
                class="pt-0 pb-0"
                cols="8"
              >
                <v-text-field
                  dense
                  v-model="minDays"
                ></v-text-field>
              </v-col>
              <v-col
                class="pt-0 pb-0"
                cols="4"
              >
                <p class="title">Оплата, дней до заезда</p>
              </v-col>
              <v-col
                class="pt-0 pb-0"
                cols="8"
              >
                <v-text-field
                  dense
                  v-model="pay"
                ></v-text-field>
              </v-col>
              <v-col
                class="pt-0 pb-0"
                cols="4"
              >
                <p class="title">Трансфер</p>
              </v-col>
              <v-col
                class="pt-0 pb-0"
                cols="8"
              >
                <v-text-field
                  dense
                  v-model="transfer"
                ></v-text-field>
              </v-col>
            </v-row>
          </v-card>
          <v-btn color="primary">
            Сохранить
          </v-btn>
          <v-btn text>Выход</v-btn>
        </v-stepper-content>
      </v-stepper-items>
    </v-stepper>
  </v-container>
</template>

<script>
  export default {
    name: 'ObjectInformation',
    data () {
      return {
        e1: 1,
        jurName: null,
        benificareName: null,
        type: null,
        phone: null,
        email: null,
        contactName: null,
        stdTimeToIn: null,
        stdTimeToOut: null,
        cancelPerm: null,
        minDays: null,
        pay: null,
        transfer: null,
        name: null,
      }
    },
  }
</script>

<style scoped>
  .title {
    font-size: 18px !important;
    font-weight: 500 !important;
    color: orange !important;
    padding-bottom: 0 !important;
    padding-top: 0 !important;
  }
</style>
